import logger from './Logger';

export function logIt (logBody) {
  try {
    logger.push({
      logBody,
      git_hash: window.git_hash,
      widget: 'entry',
      user_agent: navigator.userAgent,
      url: window.location.href,
      storeganise_url: process.env.REACT_APP_STOREGANISE_API_BASE_URL
    });
  } catch (error) {
    console.log(error);
  }
}
